.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@-moz-document url-sufix("NewsModel") {

  .moz-reader-content {
    text-align: justify;
  }
  body.sans-serif{
    font-family: "Myriad Pro";
  }
  body.serif{
    font-family: "Adobe Garamond Pro";
  }
  body.light {
    color: #1a2030;
    background-color: #f9f6f0;
  }
  body.dark {

  }
  body.sepia {

  }
}
